function urlParamsObjTranslator(data,type){
    var type=type||'objToUrl';
    var result=null;

    if(type=='urlToObj'){
        //a=1&b=2&c=3  =>  {a:1,b:2,c:3}
        if(typeof data == 'string'){
            var dataArray=data.split('&');
            result={};
            for(var i=0;i<dataArray.length;i++){
                var dataStr=dataArray[i],
                    dataKey=dataStr.split('=')[0],
                    dataValue=dataStr.split('=')[1];

                result[dataKey]=dataValue;
            }
        }else{
            console.log('urlToObjdataa=x&b=xx');
        }
    }else{
        //objToUrl {a:1,b:2,c:3}  => a=1&b=2&c=3
        result=[];
        if(typeof data == 'object') {
            for (var key in data) {
                // //
                // var tmpStr=key+'='+encodeURIComponent(data[key]);
                // // var tmpStr=key+'='+data[key];
                // result.push(tmpStr);

            	if(data[key]){
            		//todo 
                    //encodeURIComponent
                    var tmpStr=key+'='+encodeURIComponent(data[key]);
        
                    result.push(tmpStr);
				}else {
                    var tmpStr = key + '=' + "";
                    result.push(tmpStr);
                }
            }
            result=result.join('&');
        }else{
            console.log('objToUrldata');
        }
    }

    return result;
}

exports.urlParamsObjTranslator=urlParamsObjTranslator;


