const state = {
  showMenuList: true,       //true
  // content1
  basicInfo: {},            //
  personInfoList: [],       //
  filiationInfoList: [],    //
  alterInfoList: [],        //
  bizLicenceList: [],       //
  bizAnnualReportList: [],  //
  Tel: '',                  //
  Email: '',                //
  // content2
  shareHolderInfoList: [],  //
  // content3
  entinvInfoList: [],       //
  // content4
  bizTrademarkList: [],     //
  bizPatentList: [],        //

  // content5
  caseInfoList: [],         //
  bizOpExceptionList: [],   //
  sharesImpawnInfoList: [], //
  morDetailInfoList: [],    //
  bizBlackList: [],         //
  bizLogoutList: [],        //
  // content7-9
  // punishBreakInfoList: [],  //
  bizJusticeEquityList: [], //
  bizJudgmentDocList: [],   //
  bizUncreditExecutorList: [],   //

  // Loading,
  //  yes  no
  bizDetailLoading: 'yes',
  bizJusticeEquityLoading: 'yes',
  bizJudgmentDocLoading: 'yes',
  bizBlackListLoading: 'yes',
  bizLicneceLoading: 'yes',
  bizAnnualReportListLoading: 'yes',
  bizTrademarkListLoading: 'yes',
  bizPatentListLoading: 'yes',
  bizOpExceptionListLoading: 'yes',
  bizLogoutListLoading: 'yes',
  bizUncreditExecutorLoading: 'yes',
  bizBriefInfoLoading: 'yes'

}

const mutations = {
  // Loading
  updateBizDetaiLoading(state, value) {
    state.bizDetailLoading = value
  },
  updateBizJusticeEquityLoading(state, value) {
    state.bizJusticeEquityLoading = value
  },
  updateBizJudgmentDocLoading(state, value) {
    state.bizJudgmentDocLoading = value
  },
  updateBizBlackListLoading(state, value) {
    state.bizBlackListLoading = value
  },
  updateBizLicneceLoading(state, value) {
    state.bizLicneceLoading = value
  },
  updateBizAnnualReportListLoading(state, value) {
    state.bizAnnualReportListLoading = value
  },
  updateBizTrademarkListLoading(state, value) {
    state.bizTrademarkListLoading = value
  },
  updateBizPatentListLoading(state, value) {
    state.bizPatentListLoading = value
  },
  updateBizOpExceptionListLoading(state, value) {
    state.bizOpExceptionListLoading = value
  },
  updateBizLogoutListLoading(state, value) {
    state.bizLogoutListLoading = value
  },
  updateBizUncreditExecutorLoading(state, value) {
    state.bizUncreditExecutorLoading = value
  },
  updateBizBriefInfoLoading(state, value) {
    state.bizBriefInfoLoading = value
  },

  // 
  updateShowMenuList (state, value) {
    state.showMenuList = value
  }, 
  updateBasicInfo (state, value) {
    state.basicInfo = Object.assign({}, value)   
  },
  updatePersonInfoList (state, value) {
    state.personInfoList = value.concat()
  },
  updatefiliationInfoList (state, value) {
    state.filiationInfoList = value.concat()                     
  },
  updateAlterInfoList (state, value) {
    state.alterInfoList = value.concat()
  },
  updateCaseInfoList (state, value) {
    state.caseInfoList = value.concat()
  },
  updateSharesImpawnInfoList (state, value) {
    state.sharesImpawnInfoList = value.concat()
  },
  updateMorDetailInfoList (state, value) {
    state.morDetailInfoList = value.concat()
  },
  // updatePunishBreakInfoList (state, value) {
  //   state.punishBreakInfoList = value.concat()
  // },
  updateBizJusticeEquityList (state, value) {
    state.bizJusticeEquityList = value.concat()
  },
  updateBizJudgmentDocList (state, value) {
    state.bizJudgmentDocList = value.concat()
  },
  updateBizBlackList (state, value) {
    state.bizBlackList = value.concat()
  },
  updateShareHolderInfoList (state, value) {
    state.shareHolderInfoList = value.concat()
  },
  updateEntinvInfoList (state, value) {
    state.entinvInfoList = value.concat()
  },
  updateBizLicenceList (state, value) {
    state.bizLicenceList = value.concat()
  },
  updateBizAnnualReportList (state, value) {
    state.bizAnnualReportList = value.concat()
  },
  updateBizTrademarkList (state, value) {
    state.bizTrademarkList = value.concat()
  },
  updateBizPatentList (state, value) {
    state.bizPatentList = value.concat()
  },
  updateBizOpException (state, value) {
    state.bizOpExceptionList = value.concat()
  },
  updateBizLogoutList (state, value) {
    state.bizLogoutList = value.concat()
  },
  updateBizUncreditExecutorList (state, value) {
    state.bizUncreditExecutorList = value.concat()
  },
  updateTel (state, value) {
    state.Tel = value
  },
  updateEmail (state, value) {
    state.Email = value
  }

}

export default {
  state,
  mutations
}


